 ;(function( window, document, undefined ) {
	'use strict';

	// General Functions

	function alerts() {

		// Function forEach with callback
		function hello() {
			console.log( 'Iceberg is ready' );
		}

		// Globals
		return {
			hello: hello
		};

	}

	// Globals
	window.iceberg = window.iceberg || {};
  	window.iceberg.alerts = alerts();

})( window, document );
